@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@200;300;400;500&display=swap);
.form__group {
  position: relative;
  padding: 15px 0 0;
  margin-top: 0;
  margin-bottom: 0.7rem;
  width: 50%;
}

.form__field {
  font-family: inherit;
  width: 100%;
  border: 0;
  border-bottom: 2px solid #9b9b9b;
  outline: 0;
  font-size: 1.3rem;
  color: #aec1c5;
  padding: 7px 0;
  background: transparent;
  transition: border-color 0.2s;
}
.form__field::-webkit-input-placeholder {
  color: transparent;
}
.form__field::placeholder {
  color: transparent;
}
.form__field:placeholder-shown ~ .form__label {
  font-size: 1.3rem;
  cursor: text;
  top: 20px;
}

.form__label {
  position: absolute;
  top: 0;
  display: block;
  transition: 0.2s;
  font-size: 1rem;
  color: #9b9b9b;
}

.form__field:focus {
  padding-bottom: 6px;
  font-weight: 700;
  border-width: 3px;
  border-image: linear-gradient(to right, #fcbd7a, #efdd38);
  border-image-slice: 1;
}
.form__field:focus ~ .form__label {
  position: absolute;
  top: 0;
  display: block;
  transition: 0.2s;
  font-size: 1rem;
  color: #fcbd7a;
  font-weight: 700;
}

/* reset input */
.form__field:required, .form__field:invalid {
  box-shadow: none;
}

.cover {
  background-image: radial-gradient( circle farthest-corner at 10% 20%,
  #35D07F 0%, #2aa666 47.7%, #207d4c 92.3% );
  border-radius: .5em;
}
.vh-80 {
  height: 80vh!important;
}

.cover-page{
 width: 100%;
 height: 100vh;
 text-align: center; 
}

.cover-page img{
  width: 70%;
  height: 70vh;
}


:root{
  --gold-color: #fcbd7a;
  --dark-blue: #022646;
  --faded-white: #aec1c5;
}

html{
  scroll-behavior: smooth;
}
body{
  background-color: #022646;
  background-color: var(--dark-blue);
  margin: 0rem;
  padding: 0rem;
  font-family: 'Montserrat', sans-serif;
  position: relative;
  color: #aec1c5;
  color: var(--faded-white);
}

a {
  text-decoration: underline;
  color: #ffd7ad;
}


.hero{
  position: relative;
}

.hero_dao{
  color: #fcbd7a;
  color: var(--gold-color);
  font-weight: 400;
  margin: 8rem 0 0rem 10rem;
  height: 80vh;
  position: relative;
}

.dao_showcase{
  font-size: 5rem;
  padding-left: 5% !important;
}

.logo{
  color: #aec1c5;
  color: var(--faded-white);
  margin-bottom: 1.5rem;
  margin-left: -2rem;
  position: relative;
  z-index: 100;
}

.logo::before{
  position: absolute;
  left: -20px;
  top: -50%;
  content: "";
  width: 2rem;
  height: 2rem;
  border-radius: 50%;
  border: 2px solid #fcbd7a;
  border: 2px solid var(--gold-color);
  z-index: -10;
  opacity: 0.4;
}

.options{
  display: flex;
  align-items: flex-end;
  flex-direction: column;
  margin-top: 4rem;
}

.options-nav{
  display: block !important;
  color: #aec1c5 !important;
  color: var(--faded-white) !important;
  border: 1px solid #fcbd7ad0 !important;
  border-radius: 0 !important;
}

.options-nav:hover{
  background-color: #fcbd7a25 !important;
}

.disconnect{
  position: absolute;
  bottom: 2rem;
  left: 2rem;
}


.menu{
  position: fixed;
  top: 2rem;
  right: 2rem;
  width: 6rem;
  height: 40px;
  cursor: pointer;
  border-radius: 5px;
  padding: 0.2rem;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: .2s ease-in-out;
}

.menu:hover{
  background-color: #fcbd7a3a;
}

.menu-dots{
  width: 6px;
  height: 6px;
  border-radius: 50%;
  border: 1px solid #aec1c5;
  border: 1px solid var(--faded-white);
  display: inline-block;
  margin: 1.5px;
}

.one{
  margin-left: 0.5rem;
}

.two{
  margin-top: -4px;
}

.active .menu-dots{
  background-color: #aec1c5;
}


.option{
  margin: 2rem 0 3rem 0;
  padding-top: 2rem;
}

.title{
  font-size: 6rem;
  margin-bottom: 1rem;
}



@media (max-width: 700px) {
  .hero_dao{
    margin: 8rem 0 0 5rem;
  }

  .dao_showcase{
    font-size: 2rem;
  }

  .options{
    align-items: flex-start;
  }

  .title{
    font-size: 3rem;
  }

  .option{
    margin: 2rem 0 2rem 0;
  }
}
